.footer {
	padding: 40px 0 25px;
	@include sm {
		padding: 20px; }
	&__ad {
		font-size: 16px;
		font-weight: 700;
		text-align: right;
		line-height: 1;
		color: #a8a8a8;
		@include sm {
			font-size: 12px; }
		a {
			color: inherit;
			text-decoration: none;
			transition: color .3s;
			&:hover {
				color: darken(#a8a8a8, 40%); } } } }
